import interfaces from '../../utils/interfaces.js';
import { mapMutations } from 'vuex';

export default {
	data() {
		return {
			allAddress: [],
			selectIndex: 0,
			userId: ''
		};
	},
	onLoad() {
		this.initData();
	},
	methods: {
		...mapMutations({
			setUserInfo: 'SET_USER_INFO'
		}),
		initData() {
			try {
				const value = uni.getStorageSync('ele_login');
				this.userId = value.userId;
				if (value) {
					this.request({
						url: interfaces.getUserAddress + '/' + this.userId,
						success: res => {
							this.allAddress = res.myAddress;
						}
					});
				}
			} catch (e) {
				console.log(e);
			}
		},
		setAddressInfo(address, index) {
			this.selectIndex = index;
			// 将address对象存储到vuex
			this.setUserInfo(address);
			uni.navigateTo({
				url: '../orders/orders'
			});
		},
		handleDelete(address, index) {
			this.request({
				url: interfaces.deletAddress + '/' + this.userId + '/' + address._id,
				method: 'delete',
				success: res => {
					this.allAddress.splice(index, 1);
				}
			});
		},
		addAddress() {
			let detail = {
				title: '添加地址',
				addressInfo: {
					name: '',
					sex: '',
					phone: '',
					bottom: '',
					address: '',
					tag: ''
				}
			};
			uni.navigateTo({
				url: '../addAddress/addAddress?params=' + encodeURIComponent(JSON.stringify(detail))
			});
		},
		handleEdit(address) {
			let detail = {
				title: '编辑地址',
				addressInfo: address
			};
			uni.navigateTo({
				url: '../addAddress/addAddress?params=' + encodeURIComponent(JSON.stringify(detail))
			});
		}
	}
};
